body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-saira-condensed: "Saira Condensed";
  --font-inter: Inter;

  /* font sizes */
  --font-size-21xl: 40px;

  /* Colors */
  --color-white: #fff;
  --color-red-100: #ff0000;
  --color-lightseagreen: #74bfb2;
  --color-whitesmoke: #f4f3f3;
  --top: #1b1717;

  /* Paddings */
  --padding-3xs: 10px;
  --padding-xl: 20px;
}
